<!--New restaurant -->
<template>
  <div class="card">
    <div class="card-header">最新餐廳</div>
    <div class="card-body">
      <div v-for="restaurant in restaurant_list" :key="restaurant.id">
        <h4>
          <router-link
            :to="{ name: 'restaurant', params: { id: restaurant.id } }"
            >{{ restaurant.name }}</router-link
          >
          &nbsp;
          <small>{{
            restaurant.Category ? restaurant.Category.name : "未分類"
          }}</small>
        </h4>
        <p>{{ restaurant.description }}</p>
        {{ restaurant.createdAt | fromNow }}
        <hr />
      </div>
    </div>
  </div>
</template>

<script>
import { fromNowFilter } from "./../utilitys/mixins";

export default {
  props: {
    restaurant_list: {
      type: Array,
      required: true,
    },
  },
  mixins: [fromNowFilter],
};
</script>
<style scoped>
small {
  font-size: 10px;
}
</style>